@import url("https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap");
* {
	border: 0;
	padding: 0;
	box-sizing: border-box;
}
body {
	display: flex;
	justify-content: center;
	font-family: "Comfortaa", cursive;
	background-color: #07689f;
}
.main {
	width: 40vw;
	padding: 10px;
	background-color: #07689f;
	border-radius: 5px;
}
.input-field {
	margin: 5px;
	padding: 7px;
	font-family: "Comfortaa", cursive;
	border-radius: 5px;
}
form {
	margin: 10px;
	padding: 25px 0px 0px 25px;
	background-color: #a7ff83;
	border-radius: 5px;
}
button {
	padding: 10px;
	margin: 20px;
	font-family: "Comfortaa", cursive;
	border-radius: 5px;
	background-color: #17b978;
}
.list-element {
	padding: 25px 0px 0px 25px;
	margin: 10px;
	background-color: #a2d5f2;
	border-radius: 5px;
}
.center {
	display: flex;
	justify-content: center;
	align-items: center;
	color: white;
}
